import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import NavSearch from "../header/components/searchBar"
import { actionCreators } from "./store";
import * as toolbarTypes from "./store/constants"
import { actionCreators as loginActionCreators } from "../../pages/login/store";
import { actionCreators as HomeContentCreator } from "../../pages/home/store"
import DetailPageToolBar from "../header/components/detailToolBar"
import WriteToolBar from "./components/writeToolBar"

import {
    HeaderWrapper,
    NonHomeItemWrapper,
    NavItem,
    SearchWrapper,
    SearchInfo,
    SearchInfoTitle,
    SearchInfoSwitch,
    SearchInfoList,
    SearchInfoItem,
    Addition,
    Button,
    TopicItem,
    TopicWrapper,
    NavWrapper,
    NavItemShowButton,
    HeaderLineWrapper,
    TopicLineWrapper as ToolBarWrapper,
    LogoWrapper
} from "./style";

class Header extends Component {
    constructor(props) {
        super(props)
        this.state = {
            NavItemShow: false,
            ActiveNo: 0,
        }
    }

    componentDidMount() {
        this.props.handleGetTopic()
    }

    showItem() {
        let preValue = this.state.NavItemShow
        this.setState({
            NavItemShow: !preValue
        })
    }

    onChangeTopic(idx) {
        const {
            handleChangeTopic
        } = this.props;

        this.setState({
            ActiveNo: idx
        })

        handleChangeTopic(idx)
    }

    getTopics() {
        const {
            topics,
            handleChangeTopic
        } = this.props;

        const {
            ActiveNo
        } = this.state;


        const newTopics = topics.toJS()
        const topicItems = []
        newTopics.map((item, idx) => {
            topicItems.push(
                <TopicItem idx={idx} active={ActiveNo} onClick={() => this.onChangeTopic.bind(this)(idx)} key={idx}>
                    {item.domain}
                </TopicItem>
            )
        })

        return topicItems
    }


    render() {
        const {
            login,
            logout,
            showToolBar,
            handleShowLogin,
            handleShowRegister,
            toolbar
        } = this.props;
        return (
            <div>
                <HeaderLineWrapper>
                    <HeaderWrapper>
                        <Link to="/">
                            <LogoWrapper>
                                <div></div>
                                <div></div>
                            </LogoWrapper>
                        </Link>
                        <NavWrapper>
                            <Link to="/">
                                <NavItem className="active home"></NavItem>
                            </Link>
                            <NavItemShowButton className="arrow" onClick={this.showItem.bind(this)}></NavItemShowButton>
                            <NonHomeItemWrapper show={this.state.NavItemShow}>
                                <Link to="/write">
                                    <NavItem className="left"></NavItem>
                                </Link>
                                <NavItem className="right">
                                    
                                </NavItem>
                            </NonHomeItemWrapper>
                        </NavWrapper>
                        <NavSearch />
                        <Addition>
                            {login ? (
                                <Button className="writting" onClick={logout}>
                                    
                                </Button>
                            ) : (
                                    <Button className="writting" onClick={handleShowLogin}>
                                        
                                    </Button>
                                )}
                            <Button className="reg" onClick={handleShowRegister}></Button>
                        </Addition>
                    </HeaderWrapper>
                </HeaderLineWrapper>

                <ToolBarWrapper show={toolbar==toolbarTypes.ARTICLE_TOOL_BAR}>
                    <DetailPageToolBar />
                </ToolBarWrapper>

                <ToolBarWrapper  show={toolbar==toolbarTypes.TOPIC_TOOL_BAR}>
                    <TopicWrapper>
                        {this.getTopics()}
                    </TopicWrapper>
                </ToolBarWrapper>

                <ToolBarWrapper show={toolbar==toolbarTypes.WRITE_TOOL_BAR}>
                    <WriteToolBar/>
                </ToolBarWrapper>


            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        focused: state.getIn(["header", "focused"]),
        list: state.getIn(["header", "list"]),
        page: state.getIn(["header", "page"]),
        totalPage: state.getIn(["header", "totalPage"]),
        mouseIn: state.getIn(["header", "mouseIn"]),
        login: state.getIn(["login", "login"]),
        topics: state.getIn(["home", "topics"]),
        tags: state.getIn(["header", "tags"]),
        showToolBar: state.getIn(["detail", "showToolBar"]),
        toolbar: state.getIn(["header", "toolbar"])
    };
};

const mapDispathToProps = dispatch => {
    return {
        handleInputFocus(list) {
            list.size === 0 && dispatch(actionCreators.getList());
            dispatch(actionCreators.searchFocus());
        },
        handleInputBlur() {
            dispatch(actionCreators.searchBlur());
        },
        handleMouseEnter() {
            dispatch(actionCreators.mouseEnter());
        },
        handleMouseLeave() {
            dispatch(actionCreators.mouseLeave());
        },
        handleChangePage(page, totalPage, spin) {
            let originAngle = spin.style.transform.replace(/[^0-9]/gi, "");
            if (originAngle) {
                originAngle = parseInt(originAngle, 10);
            } else {
                originAngle = 0;
            }
            spin.style.transform = "rotate(" + (originAngle + 360) + "deg)";

            if (page < totalPage) {
                dispatch(actionCreators.changePage(page + 1));
            } else {
                dispatch(actionCreators.changePage(1));
            }
        },
        handleGetTopic() {
            dispatch(HomeContentCreator.getTopics())
        },
        logout() {
            dispatch(loginActionCreators.logout());
        },
        handleChangeTopic(idx) {
            dispatch(HomeContentCreator.setTopic(idx))
        },
        handleShowLogin() {
            dispatch(loginActionCreators.showPopup(true))
        },
        handleShowRegister() {
            dispatch(loginActionCreators.showPopup(false))
        }
    };
};

export default connect(
    mapStateToProps,
    mapDispathToProps
)(Header);
