import React, { Component } from 'react'
import Editor from 'for-editor'
import { connect } from 'react-redux'
import { actionCreators as headerActionCreators } from "../../../../common/header/store"
import { WRITE_TOOL_BAR } from "../../../../common/header/store/constants"
import Toolbar from "../toolbar"

class WebSiteEditor extends Component {
    constructor() {
        super()
        this.state = {
            value: ''
        }
        this.handleChange=this.handleChange.bind(this)
    }

    handleChange(value) {
        console.log(value)
        this.setState({
            value
        })
        this.props.contentChange(value)
    }

    componentDidMount() {
        this.props.showToolBar()
    }

    render() {
        let toolbar = {
            img: true, // 
            link: true, // 
            code: true, // 
            preview: true, // 
            expand: true, // 
            /* v0.0.9 */
            undo: true, // 
            redo: true, // 
            save: true, // 
            /* v0.2.3 */
            subfield: true, // 
        }
        const { value } = this.state
        return (
            <div>
                <Toolbar/>
                <Editor value={value} onChange={(value) => this.handleChange(value)} toolbar={toolbar} />
            </div>
        )
    }
}

const mapState = state => ({

})

const mapDispatch = dispatch => ({
    showToolBar() {
        dispatch(headerActionCreators.changeToolBar(WRITE_TOOL_BAR))
    },
    contentChange(value){
        dispatch(headerActionCreators.contentChange(value))
    }
})

export default connect(
    mapState,
    mapDispatch
)(WebSiteEditor)